
.header__container {
    position: absolute;
    display: flex;
    z-index: 5;
    align-items: center;
    margin-top: 8px;

    @include mq(tablet) {
      display: flex;
      margin-top: 30px;
    }
}

.header__btn-container{
    
  @include mq(tablet) {
    display: flex;
    margin-left: 293px;
    }

  @include mq(desktop) {
    display: flex;
    margin-left: 141px;
    }
}


 .manu-icon{
        background-color: rgba(0,0,0,0);
        border: none;
        margin-left: auto;
        transition-property: color;
        transition-duration: .25s;
        transition-timing-function: cubic-bezier(.4,0,.2,1);
        margin-left: 61px;
        padding: 0;
        @include mq(tablet) {
          margin-left: 0;
      }

      @include mq(desktop) {
          display: none;
      }
  }

  
    .header__nav-item{
        font-style: "DM Sans","Sans-serif";
        border: 1px solid #e18298;
        align-items: center;
        line-height: 1.28571;
        }

    .header__nav-link{
        display: block;
        padding-left: 20px;

        font-weight: 700;
        font-size: 14px;
        line-height: calc(18 / 14);

        color: var(--white-text-color);
        padding-top: 12px;
        padding-bottom: 12px;
        transition-property: color;
        transition-duration: .25s;
        transition-timing-function: cubic-bezier(.4,0,.2,1);
       
    }

.header__buy-button {
  display: block;
  padding-right: 20px;
  font-family: 'DM Sans', 'Sans-serif';
  font-weight: 700;
  font-size: 16px;
  line-height: calc(21 / 16);
  height: 44px;
  border-radius: 22px;
  color: var(--red-text-color);
  border-color: transparent;
  background-color: var(--white-text-color);
  background-image: url('../images/hero/arrow-2.svg');
  background-repeat: no-repeat;
  background-size: 10px 10px;
  background-position: right 37px top 16px;
  transition-property: background-color;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 250ms;
  cursor: pointer;
  &:focus,
  &:hover {
    color: var(--white-text-color);
    background-color: var(--red-text-color);
    background-image: url('../images/hero/arrow.svg');
  }
  @include mq(mobile-only) {
    display: none;
  }
  @include mq(tablet) {
    width: 160px;
    margin-left: 32px;
    background-position: right 33px top 16px;
  }
  @include mq(desktop) {
    background-position: right 25px top 16px;
    display: block;
    margin: 0;  
  }
}


.header__nav-desktop{
    display: none;

    @include mq(tablet) {
          display: none;
      }

      @include mq(desktop) {
          display: block;
          margin-left: 128px;
      }
  }

  .header__nav-item-desktop:not(:last-child){
    margin-right: 35px;
  }

  .header__nav-list-desktop{
    display: flex;
  }

  .header__nav-link-desktop{
    font-weight: 700;
    font-size: 16px;
    line-height: 21px;
    display: flex;
    align-items: center;

    color: var(--white-text-color);
  }

  .header__nav-link-current {
  color: var(--red-text-color);
}

